/* --------------- layout -------------- */

:root {
  margin: 0;
  --main-pink: #ffc0cb;
  --page-pink: #faacbc;
  --parent-pink: #f6a1b2;
  --child-pink: #f9cad4;
  --hover-white: #f9dae1;
  --icon-border-grey: #cab3c6;
  --parent-border-grey: #998888;
  --parent-border: solid 1px var(--parent-border-grey);
  --child-border-grey: #aa90a0;
  --child-border: solid 1px var(--child-border-grey);
  --checkmark-bkgnd: #e2e2e2;
  --hover-border-color: #EEEE77FF;
}

.app-background {
  background-color: rgb(171, 135, 119);
  background-image: url("../img/waffle1.jpg");
  background-repeat: repeat;
  background-size: 50%;
}

.app-layout {
  background-color: rgba(171, 135, 119, .66);
  min-height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.app-header {
  background-color: black;
  color: teal;
  height: 9rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 1;
  /*max-width: 1200px;*/
  width: 100%;
  z-index: 200;
}

.app-footer {
  background-color: black;
  color: teal;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  /*position: sticky;
  bottom: 0;*/
  width: 100%;
  z-index: 200;
}

.app-main {
  background-color: var(--main-pink);
  max-width: 1200px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0;

  display: grid;
  justify-content: center;
  flex: 1;
  /*one big fat row...the page section will likely be column but the default is flex row with justify center*/
  /*the width is set to 100% to add fake child margin and child sections (page-section class) are max width 960px*/
  /*the flex grow 1 makes to row fill the height and pushes the sticky footer to the bottom of the page*/
}

.page-section {
  background-color: var(--page-pink);
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  padding-left: 0rem;
  padding-right: 0rem;
  max-width: 960px;
  width: 100%;
}

.parent-article {
  background-color: var(--parent-pink);
  border: var(--parent-border);
  border-radius: 0.5rem;
  padding: 0.5rem;
  margin-top: .5rem;
  margin-bottom: .5rem;
  margin-left: .25rem;
  margin-right: .25rem;
}

.child-article {
  background-color: var(--child-pink);
  border: var(--child-border);
  border-radius: 0.5rem;
  padding: 0.5rem;
  margin-top: .25rem;
  margin-bottom: .25rem;
  margin-left: .25rem;
  margin-right: .25rem;
}

.parent-button {
  padding: 1rem;
  border-radius: 1rem;
  border: solid 3px rgb(250, 172, 188);
  font-size: 1.2rem;
  margin-left: 0.25rem;
  margin-right: 0.25rem;
  cursor: pointer;
  background-color: rgb(249, 202, 212);
  box-shadow: 0px 0px 18px white;
}

.svg-arrow {
  width: 2.4rem;
  fill: rgb(249, 202, 212);
  stroke: rgb(249, 202, 212);
  margin-left: 0.25rem;
  margin-right: 0.25rem;
}

.hover-wrapper:hover .svg-arrow {
  fill: var(--hover-white);
  stroke: var(--hover-white);
}

.hover-wrapper:hover .parent-button {
  background-color: var(--hover-white);
  box-shadow: none;
  border: solid 3px var(--hover-border-color);
}

.hover-wrapper:hover .child-article {
  background-color: var(--hover-white);
  outline: solid 2px var(--hover-border-color);
}

.hover-wrapper:hover .toppings-checkmark {
  border: solid 3px var(--hover-border-color);
}

/* ------- layout specific ------------ */

.toppings-row {
  display: grid;
  grid-template-columns: 3em 4em 8em;
  align-items: center;
}

.toppings-col {
  display: grid;
  grid-template-columns: 5em;
  justify-items: center;
  grid-gap: 1em;
}

.toppings-img2 {
  border-radius: 2em;
  border: var(--child-border);
  width: 2em;
  height: 2em;
}

.toppings-img3 {
  border-radius: 3em;
  border: solid 3px var(--icon-border-grey);
  width: 3em;
  height: 3em;
}

.toppings-img5 {
  border-radius: 5em;
  border: solid 3px var(--icon-border-grey);
  width: 5em;
  height: 5em;
}

.toppings-img5-sel {
  border-radius: 5em;
  border: solid 3px var(--icon-border-grey);
  background: var(--hover-white);
  width: 5em;
  height: 5em;
}

.toppings-checkmark {
  border-radius: 2em;
  border: solid 3px rgb(202, 179, 198);
  width: 2em;
  background: var(--checkmark-bkgnd);
  cursor: pointer;
}

.number-circle {
  border: 1px solid black;
  border-radius: .75rem;
  background-color: rgb(250, 172, 188);
  padding: 0.25rem;
}

.home-img1 {
  max-height: 6rem;
}

.kevmetz {
  color: firebrick;
}

.kevmetz:hover {
  color: var(--hover-white);
}

.logo-header {
  max-height: 8rem;
  margin-top: .5rem;
}

.logo-footer {
  height: 2rem;
  margin: 0.5rem;
}